import Link from 'next/link'
const Navbar = () => (
    <nav>
        <ul>
            <li><Link href='/'><a>Home</a></Link></li>
            <li><Link href='/about'><a>About</a></Link></li>
        </ul>
        <style jsx>{`
            ul{
                background-color: #333;
                display:flex;
            }
            ul li{
                list-style: none
            }
            ul li a{
                color: #fff;
                text-decoration: none;
            }
        `}
        </style>
    </nav>
)

export default Navbar